import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

// redex
import { connect } from 'react-redux'
import { loadQuestions } from '../../redux/store'

// components
import Loading from '../../components/loading/Loading'

class AdminQuestions extends Component {
   // state
   state = {
      loadingButton: {},
      loadPage: false
   }

   // method
   componentDidMount = () => {
      this.props.dispatch(loadQuestions())
      setTimeout(() => {
         this.setState({ loadPage: true })
      }, 1000)
   }

   deteteQuestion = _id => {
      let loadingButton = this.state.loadingButton
      loadingButton[_id] = 'is-loading'
      this.setState({ loadingButton })

      axios({
         url: `/api/question/delete/${_id}`,
         method: 'DELETE'
      }).then(res => {
         console.log(res.data)
         this.props.dispatch(loadQuestions())
      })
   }

   // view
   questionsList = questions => {
      return questions.map(item => {
         return (
            <tr key={item._id}>
               <th style={{ paddingLeft: '11%' }}>
                  <Link to={`/question/${item._id}`}>{item.title}</Link>
               </th>
               <th style={{ textAlign: 'center' }}>
                  <button
                     onClick={() => this.deteteQuestion(item._id)}
                     className={`button is-danger ${
                        this.state.loadingButton[item._id]
                     }`}>
                     Del.
                  </button>
               </th>
            </tr>
         )
      })
   }

   // render
   render() {
      return (
         <div className="container">
            {/* Loading Page */}
            {this.state.loadPage ? (
               <div className="column">
                  {/* Questions Admin */}
                  <h3>Questions Admin</h3>
                  <table className="table is-fullwidth">
                     <thead>
                        <tr>
                           <th style={{ paddingLeft: '10%' }}>Questions</th>
                           <th
                              style={{
                                 width: '40%',
                                 textAlign: 'center'
                              }}>
                              Action
                           </th>
                        </tr>
                     </thead>
                     <tbody>
                        {/* questions list */}
                        {this.questionsList(this.props.questions)}
                        {/* --- */}
                     </tbody>
                  </table>
                  {/* --- */}
               </div>
            ) : (
               <Loading />
            )}
            {/* --- */}
         </div>
      )
   }
}

export default connect(state => state)(AdminQuestions)



