import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Fade from 'react-reveal/Fade'

// redux
import { connect } from 'react-redux'

class QuestionsList extends Component {
   // view
   questionsList = questions => {
      return questions.map(item => {
         return (
            <Fade key={item._id}>
               <div className="column">
                  <Link
                     className="box has-text-dark"
                     to={`/question/${item._id}`}>
                     <p>คำถาม : {item.title}</p>
                     <small>คำตอบ : {item.answers.length}</small>
                     <br />
                     <small>
                        {new Date(item.created).toLocaleDateString()}
                     </small>
                  </Link>
               </div>
            </Fade>
         )
      })
   }

   // render
   render() {
      return (
         <div className="column">
            <div
               className="box has-background-info has-text-white"
               style={{ border: '2px white solid' }}>
               <b>คำถาม</b>
               {this.questionsList(this.props.questions)}
            </div>
         </div>
      )
   }
}

export default connect(state => state)(QuestionsList)



