import React, { Component } from 'react'
import anime from '../../images/anime.gif'
export class Loading extends Component {
   // render
   render() {
      return (
         <div className="is-c">
            <img src={anime} width="80px" alt="" />
            <h3>Loading..</h3>
         </div>
      )
   }
}

export default Loading



