import React, { Component } from 'react'
import axios from 'axios'

// redux
import { connect } from 'react-redux'
import { loadQuestions } from '../../../redux/store'

class Create extends Component {
   // state
   state = {
      loadingButton: ''
   }

   // method
   createQuestion = e => {
      e.preventDefault()
      this.setState({ loadingButton: 'is-loading' })

      axios({
         url: '/api/question/create/post',
         method: 'POST',
         data: {
            title: this.refs.title.value,
            body: this.refs.body.value,
            created: new Date()
         }
      }).then(async res => {
         console.log(res.data)
         this.props.dispatch(loadQuestions())
         this.refs.createQuestion.reset()
         this.setState({ loadingButton: '' })
      })
   }

   // render
   render() {
      return (
         <div>
            <div className="column">
               <b>ตั้งกระทู้</b>
               <form ref="createQuestion">
                  <div className="column">
                     <div className="control">
                        <input
                           className="input is-primary"
                           ref="title"
                           type="text"
                           placeholder="หัวข้อคำถาม"
                        />
                     </div>
                  </div>
                  <div className="column">
                     <textarea
                        className="textarea is-primary"
                        ref="body"
                        placeholder="รายละเอียด"
                     />
                  </div>
                  <div className="column">
                     <button
                        className={`button is-warning ${
                           this.state.loadingButton
                        }`}
                        onClick={this.createQuestion}>
                        โพสต์
                     </button>
                  </div>
               </form>
            </div>
         </div>
      )
   }
}

export default connect(state => state)(Create)



