import React, { Component } from 'react'
import formData from 'form-data'
import axios from 'axios'

// redux
import { connect } from 'react-redux'
import { loadImages } from '../../../redux/store'

class Admin extends Component {
   // state
   state = {
      loadingUpload: '',
      loadingDel: {}
   }

   // method
   uploadImage = e => {
      e.preventDefault()
      this.setState({ loadingUpload: 'is-loading' })

      let form = new formData()
      form.append('img', this.refs.img.files[0])

      axios({
         url: '/api/image/upload',
         method: 'POST',
         data: form
      }).then(res => {
         console.log(res.data)
         this.refs.test.reset()
         this.setState({ loadingUpload: '' })
         this.props.dispatch(loadImages())
      })
   }

   deteteImage = _id => {
      let loadingDel = this.state.loadingDel
      loadingDel[_id] = 'is-loading'
      this.setState({ loadingDel })

      axios({
         url: `/api/image/delete/${_id}`,
         method: 'DELETE'
      }).then(res => {
         console.log(res.data)
         this.props.dispatch(loadImages())
      })
   }

   // view
   imagesTable = images => {
      return images.map(item => {
         return (
            <tr key={item._id}>
               <td>{item.filename}</td>
               <td>
                  <button
                     onClick={() => this.deteteImage(item._id)}
                     className={`button is-warning is-small ${
                        this.state.loadingDel[item._id]
                     }`}>
                     Del.
                  </button>
               </td>
            </tr>
         )
      })
   }

   // render
   render() {
      return (
         <div className="column">
            <h3>Images Admin</h3>
            <table className="table is-fullwidth">
               <thead>
                  <tr>
                     <th>Images</th>
                     <th>Action</th>
                  </tr>
               </thead>
               <tbody>
                  {/* images row */}
                  {this.imagesTable(this.props.images)}
                  {/* --- */}
               </tbody>
            </table>
         </div>
      )
   }
}

export default connect(state => state)(Admin)



