import React, { Component } from 'react'
// import formData from 'form-data'
import axios from 'axios'
import Zoom from 'react-reveal/Zoom'
import File64 from 'react-file-base64'

// redux
import { connect } from 'react-redux'
import { loadImages } from '../../../redux/store'

class Upload extends Component {
   // state
   state = {
      loadingUpload: '',
      loadingDel: {},
      file: ''
   }

   // method
   uploadImage = e => {
      e.preventDefault()
      this.setState({ loadingUpload: 'is-loading' })

      axios.post('/api/image/upload', { img: this.state.file }).then(res => {
         console.log(res.data)
         this.refs.form.reset()
         this.setState({ loadingUpload: '', file: '' })
         this.props.dispatch(loadImages())
      })
   }

   deteteImage = _id => {
      let loadingDel = this.state.loadingDel
      loadingDel[_id] = 'is-loading'
      this.setState({ loadingDel })

      axios({
         url: `/api/image/delete/${_id}`,
         method: 'DELETE'
      }).then(res => {
         console.log(res.data)
         this.props.dispatch(loadImages())
      })
   }

   //view
   imagesList = images => {
      return images.map((item, i) => {
         return (
            <Zoom key={i}>
               <div className="column is-4">
                  <div className="box">
                     <img src={`${item.path}`} alt="" />
                     <button
                        onClick={() => this.deteteImage(item._id)}
                        className={`button is-danger is-small ${
                           this.state.loadingDel[item._id]
                        }`}>
                        Del.
                     </button>
                  </div>
               </div>
            </Zoom>
         )
      })
   }

   // render
   render() {
      return (
         <div className="column">
            <h3>Upload Image</h3>
            <div
               className="columns"
               style={{ margin: 30, justifyContent: 'center' }}>
               <form ref="form">
                  <File64 onDone={file => this.setState({ file })} />
                  <button
                     className={`button is-info is-small ${
                        this.state.loadingUpload
                     }`}
                     onClick={this.uploadImage}>
                     Upload
                  </button>
               </form>
            </div>
            <div className="columns is-multiline">
               {/* images list */}
               {this.imagesList(this.props.images)}
               {/* --- */}
            </div>
         </div>
      )
   }
}

export default connect(state => state)(Upload)



