import React, { Component } from 'react'
import formData from 'form-data'
import axios from 'axios'

// redux
import { connect } from 'react-redux'
import { loadImage360s } from '../../../redux/store'

class Admin extends Component {
   // state
   state = {
      loadingButton: {
         IS: '',
         BC: '',
         MG: '',
         AC: ''
      }
   }

   // method
   image360Save = (branch, ref) => {
      let loadingButton = this.state.loadingButton
      loadingButton[branch] = 'is-loading'
      this.setState({ loadingButton })

      let form = new formData()
      form.append('branch', branch)
      form.append('file', ref.files[0])

      axios({
         url: '/api/image360/create',
         method: 'POST',
         data: form
      }).then(res => {
         console.log(res.data)
         ref.value = null
         loadingButton[branch] = ''
         this.setState({ loadingButton })
         this.props.dispatch(loadImage360s())
      })
   }

   // render
   render() {
      return (
         <div className="column">
            <h3>360 Admin</h3>
            <table className="table is-fullwidth">
               <thead>
                  <tr>
                     <th style={{ width: '50%' }}>Branch</th>
                     <th>Save or Update</th>
                  </tr>
               </thead>
               <tbody>
                  <tr>
                     <td>IS</td>
                     <td>
                        <input type="file" ref="is" />
                        <button
                           onClick={() => this.image360Save('IS', this.refs.is)}
                           className={`button is-success is-small ${
                              this.state.loadingButton.IS
                           }`}>
                           Upload
                        </button>
                     </td>
                  </tr>
                  <tr>
                     <td>BC</td>
                     <td>
                        <input type="file" ref="bc" />
                        <button
                           onClick={() => this.image360Save('BC', this.refs.bc)}
                           className={`button is-success is-small ${
                              this.state.loadingButton.BC
                           }`}>
                           Upload
                        </button>
                     </td>
                  </tr>
                  <tr>
                     <td>AC</td>
                     <td>
                        <input type="file" ref="ac" />
                        <button
                           onClick={() => this.image360Save('AC', this.refs.ac)}
                           className={`button is-success is-small ${
                              this.state.loadingButton.AC
                           }`}>
                           Upload
                        </button>
                     </td>
                  </tr>
                  <tr>
                     <td>MG</td>
                     <td>
                        <input type="file" ref="mg" />
                        <button
                           onClick={() => this.image360Save('MG', this.refs.mg)}
                           className={`button is-success is-small ${
                              this.state.loadingButton.MG
                           }`}>
                           Upload
                        </button>
                     </td>
                  </tr>
               </tbody>
            </table>
         </div>
      )
   }
}

export default connect(state => state)(Admin)



