import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Is from '../icon/isr4b.png'
import classnames from 'classnames'

class Index extends Component {
   state = {
      isActive: false,
      animate: true
   }

   componentDidMount = () => {
      this.interval = setInterval(() => {
         this.setState({ animate: !this.state.animate })
      }, 2000)
   }

   componentWillUnmount = () => {
      clearInterval(this.interval)
   }

   render() {
      return (
         <div>
            <div className="hero is-dark is-fullheight bg">
               <div className="hero-head" style={{ margin: '2rem' }}>
                  <nav className="navbar">
                     <div className="container">
                        <div
                           className={classnames({
                              'navbar-brand': true,
                              'is-active': this.state.isActive
                           })}>
                           <span className="navbar-item">
                              <img src={Is} alt="" />
                           </span>
                           <span
                              className="navbar-burger burger"
                              data-target="navbarMenuHeroA"
                              onClick={() =>
                                 this.setState({
                                    isActive: !this.state.isActive
                                 })
                              }>
                              <span />
                              <span />
                              <span />
                           </span>
                        </div>
                        <div
                           className={classnames({
                              'navbar-menu': true,
                              'is-active': this.state.isActive
                           })}
                           id="navbarMenuHeroA">
                           <div className="navbar-end">
                              <a className="navbar-item" href="/">
                                 หน้าแรก
                              </a>
                              <Link className="navbar-item" to="/is">
                                 สาขาวิชา
                              </Link>
                              <Link className="navbar-item" to="/questions">
                                 กระทู้ถามตอบ
                              </Link>
                              <span className="navbar-item">
                                 <Link
                                    className="button is-primary is-rounded"
                                    to="/login">
                                    <span className="icon">
                                       <i className="fas fa-key" />
                                    </span>
                                    <span>
                                       <b>Admin</b>
                                    </span>
                                 </Link>
                              </span>
                           </div>
                        </div>
                     </div>
                  </nav>
               </div>
               <div className="hero-body">
                  <div
                     className={classnames({
                        'container has-text-centered': true,
                        'animated pulse': this.state.animate
                     })}>
                     <h3 className="title is-3">Welcome to Our WebSite.</h3>
                     <h4 className="subtitle is-4">We hope you enjoy.</h4>
                  </div>
               </div>
               <div className="foot m-1">
                  <h3 className="animated fadeInLeft delay-1s slow">
                     Power by ISr4B
                  </h3>
               </div>
            </div>
         </div>
      )
   }
}
export default Index



