import React, { Component } from 'react'

// redux
import { connect } from 'react-redux'
import { loadImage360s } from '../../redux/store'

// components
import Loading from '../../components/loading/Loading'
import Preview from '../../components/admin/360/Preview'
import Admin from '../../components/admin/360/Admin'

class Image360 extends Component {
   // state
   state = {
      loadPage: false
   }
   // method
   componentDidMount = () => {
      this.props.dispatch(loadImage360s())
      setTimeout(() => {
         this.setState({ loadPage: true })
      }, 1000)
   }

   // render
   render() {
      return (
         <div className="container">
            {/* Loading Page */}
            {this.state.loadPage && this.props.isAuth ? (
               <div>
                  {/* 360 Admin */}
                  <Admin />
                  {/* --- */}

                  {/* 360 Preview */}
                  <Preview />
                  {/* --- */}
               </div>
            ) : (
               <Loading />
            )}
            {/* --- */}
         </div>
      )
   }
}

export default connect(state => state)(Image360)



