import React, { Component } from 'react'

// redux
import { connect } from 'react-redux'
import { loadQuestions } from '../../redux/store'

// components
import Loading from '../../components/loading/Loading'
import QuestionsList from '../../components/questions/index/QuestionsList'
import Create from '../../components/questions/index/Create'

class Questions extends Component {
   // state
   state = {
      loadPage: false
   }

   // method
   componentDidMount = () => {
      this.props.dispatch(loadQuestions())
      setTimeout(() => {
         this.setState({ loadPage: true })
      }, 1000)
   }

   // render
   render() {
      return (
         <div>
            {/* Check when load quesetions */}
            {this.state.loadPage ? (
               <div className="hero is-fullheight is-info is-bold">
                  <div className="hero-head">
                     <div className="container">
                        <span className="icon has-text-white mt-1">
                           <i className="fas fa-question" />
                        </span>
                        <b className="has-text-white">Questions</b>
                        <QuestionsList />
                        {/* Create question */}
                        <Create />
                        {/* --- */}
                     </div>
                  </div>
               </div>
            ) : (
               <Loading />
            )}
            {/* --- */}
         </div>
      )
   }
}

export default connect(state => state)(Questions)



