import React from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import { store, login } from './redux/store'
import { ToastContainer } from 'react-toastify'

// route
import Index from './pages/Index'
import Login from './pages/Login'
import List from './pages/List'
import Questions from './pages/questions/questions'
import Question from './pages/questions/question'
import AdminQuestions from './pages/admin/questions'
import Images from './pages/admin/images'
import Images360 from './pages/admin/360'
import IS from './pages/IS'
import Test from './pages/Test'

if (localStorage.getItem('isAuth')) {
   store.dispatch(login())
}

const Routers = () => (
   <Provider store={store}>
      <Router>
         <div>
            <ToastContainer autoClose={2000} />
            <Route path="/test" component={Test} />
            <Route exact path="/" component={Index} />
            <Route path="/login" component={Login} />
            <Route path="/list" component={List} />
            <Route path="/questions" component={Questions} />
            <Route path="/question/:id" component={Question} />
            <Route path="/admin/questions" component={AdminQuestions} />
            <Route path="/admin/images" component={Images} />
            <Route path="/admin/360" component={Images360} />
            <Route path="/is" component={IS} />
         </div>
      </Router>
   </Provider>
)

export default Routers



