import React, { Component } from 'react'

// redux
import { connect } from 'react-redux'

class Preview extends Component {
   // view
   panoramaList = image360s => {
      return image360s.map(item => {
         return (
            <div className="column is-6" key={item._id}>
               <div className="box">
                  <iframe
                     title="image360"
                     src={`http://localhost:4000/pano/${item.filename}`}
                     frameBorder="0"
                     width="100%"
                     height="250px"
                     scrolling="no"
                  />
                  <h3>{item.branch}</h3>
               </div>
            </div>
         )
      })
   }

   // render
   render() {
      return (
         <div className="column">
            <h3>360 Preview</h3>
            <div className="columns is-multiline">
               {/* Panarama list */}
               {this.panoramaList(this.props.image360s)}
               {/* --- */}
            </div>
         </div>
      )
   }
}

export default connect(state => state)(Preview)



