import React, { Component } from 'react'

// redux
import { connect } from 'react-redux'
import { loadImages } from '../../redux/store'

// components
import Loading from '../../components/loading/Loading'
import Upload from '../../components/admin/images/Upload'
import Admin from '../../components/admin/images/Admin'

class AdminImages extends Component {
   // state
   state = {
      loadPage: false
   }
   // method
   componentDidMount = () => {
      this.props.dispatch(loadImages())
      setTimeout(() => {
         this.setState({ loadPage: true })
      }, 1000)
   }
   // render
   render() {
      return (
         <div className="container">
            {this.state.loadPage ? (
               <div>
                  {/* Upload image */}
                  <Upload />
                  {/* --- */}

                  {/* Images Admin. */}
                  <Admin />
                  {/* --- */}
               </div>
            ) : (
               <Loading />
            )}
         </div>
      )
   }
}

export default connect(state => state)(AdminImages)



